.videoElement {
    transform: rotateY(180deg);
    -webkit-transform:rotateY(180deg); /* Safari and Chrome */
    -moz-transform:rotateY(180deg); /* Firefox */
    margin-top: 20px;
    margin-left: 10px;
    margin-right: 10px;
    display: block;
}

.img{
    transform: rotateY(180deg);
    -webkit-transform:rotateY(180deg); /* Safari and Chrome */
    -moz-transform:rotateY(180deg); /* Firefox */
    margin-top: 20px;
    display: block;
    /* float: right; */
    /* justify-self: right;
    align-self: center; */
}

