.headerContainer{
    min-height: 100px;
    min-width: 100%;
    background-color: pink;
}

.headerTitle{
    text-align: center;
    padding-top: 20px;
    font-weight: bold;
    font-size: 30px;
}

@media only screen and (max-width: 992px) {
    .headerTitle{
        font-size: 30px;
    }
}

@media only screen and (max-width: 768px) {
    .headerTitle{
        font-size: 20px;
    }
}

