.container{
    width: 80%;
    /* min-height: 600px; */
    background-color: pink;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
}

.cameraContainer {
    display: flex;
    justify-content: center;
    min-height: 70vh;
}


.openCameraBtn{   
    align-self: center;
    margin-right: 20px;
    margin-top: 20px;
    background-color: white;
    height: 30px;
    border: 1px solid;
    border-radius: 5px;
}

.openCameraBtn:hover{
    transform: scale(1.1)
}

.openCameraBtn:active{
    transform: scale(1.2)
}

@media only screen and (max-width: 992px) {
    .headerTitle{
        margin-top: 20px;
        width: 80%;
    }
}

@media only screen and (max-width: 768px) {
    .container{
        margin-top: 10px;
        width: 90%;
    }
}